*{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 40px;
}

body {
  padding: 60px;
}
input {
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}



.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow:hidden;
  margin-bottom: 10px;
  margin-right: 10px;

}

.card p {
  text-align: center;
}

.card img {
  width: 50%;
}





 
